import React from "react";
import ReactDOM from "react-dom";

import StickyScene from "./sticky-scene.js";
import { screenOrientation } from "./utils.js";

const orientation=screenOrientation.orientation;
if (orientation instanceof String)
{
	const lockOrientation=screenOrientation.lockOrientation;
	if (lockOrientation) lockOrientation("landscape");
}
else if (orientation) orientation.lock("landscape");

ReactDOM.render(
	<React.StrictMode>
		<StickyScene/>
	</React.StrictMode>,
	document.getElementById("stickySceneRoot")
);
